/* src/components/SummaryDashboard.css */
.summary-dashboard {
  display: grid;
  grid-template-columns: 1fr; /* One column layout for cards */
  gap: 1rem;
  margin-bottom: 2rem;
}

.month-picker {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.card {
  background: #fff;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.card h3 {
  margin-bottom: 0.5rem;
  font-size: 1rem;
  color: #2c3e50;
}

.card p {
  font-size: 1.5rem;
  font-weight: bold;
}

.card.categories ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.card.categories li {
  display: flex;
  justify-content: space-between;
  padding: 0.25rem 0;
  border-bottom: 1px solid #e1e4e8;
}

.card.categories li:last-child {
  border-bottom: none;
}

.category-name {
  font-size: 0.9rem;
}

.category-total {
  font-weight: 600;
}

.chart-container {
  background: #fff;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-top: 1rem;
}
.chart-container h3 {
  margin-bottom: 0.5rem;
  font-size: 1rem;
  color: #2c3e50;
}
