/* src/App.css */
.app-container {
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.app-title {
  text-align: center;
  margin-bottom: 1.5rem;
  font-size: 2rem;
  color: #2c3e50;
}
  /* Tab navigation */
.tab-nav {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}
.tab {
  flex: 1;
  padding: 0.5rem;
  background: #f1f3f5;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 500;
}
.tab.active {
  background: #2c3e50;
  color: #fff;
}


/* Responsive adjustments */
@media (max-width: 600px) {
  .app-container {
    margin: 1rem;
    padding: 0.5rem;
  }
  
  .app-title {
    font-size: 1.5rem;
  }
  
}
